import { Route, Routes } from "react-router-dom";
import Home from "../Pages/Home";
import Login from "../Pages/Login";
import SignUp from "../Pages/SignUp";
import Profile from "../Pages/Profile";
import UserMainView from "../Components/UserView/UserMainView";
import TripMainView from "../Components/TripView/TripMainView";
import TripDetails from "../Pages/TripDetails";
import RateCalculator from "../Pages/RateCalculator";

const Index = () => {
  return (
    <Routes>
      <Route path="/" exact element={<Home />} />
      <Route path="/login" element={<Login />} />

      <Route path="/signup" element={<SignUp />} />

      <Route path="/user" element={<UserMainView />} />

      <Route path="/trip/:tripId" element={<TripMainView />} />

      <Route path="/trip/:tripId/details" element={<TripDetails />} />

      <Route path="/exchangeRate" element={<RateCalculator />} />

      <Route path="/profile" element={<Profile />} />
    </Routes>
  );
};

export default Index;
