import { useDispatch, useSelector } from "react-redux";
import ExpenseList from "./Expenses/ExpenseView/ExpenseList";
import { useEffect } from "react";
import TripAppBar from "./TripAppBar";
import AddExpense from "./Expenses/ExpenseMethods/AddExpense";
import useGet from "../../Hooks/useGet";
import appConfig from "../../Utils/Config";
import { Alert, Typography } from "@mui/material";
import { setExpenseList } from "../../Redux-TS/Reducers/expenseListSlice";
import { useParams } from "react-router-dom";
import { setActive } from "../../Redux-TS/Reducers/activeTripSlice";

const TripMainView = () => {
  const trip = useSelector((state) => state.activeTrip.trip);
  const activeTripId = useSelector((state) => state.activeTrip.trip.id);
  const expenseList = useSelector((state) => state.expenseList.expenseList);
  const tripList = useSelector((state) => state.tripList.tripList);
  const dispatch = useDispatch();
  const { error, sendRequest: getExpenses } = useGet();
  const { tripId } = useParams();

  useEffect(() => {
    /* check if the tripID params are the same as the active trip.
    if not, update the active trip and get the expense list from the server.
    */

    if (parseInt(tripId) === activeTripId) {
      return;
    }
    if (parseInt(tripId) !== activeTripId) {
      dispatch(setActive(tripList.find((t) => t.id === parseInt(tripId))));
      const updateExpenses = (expenseObj) => {
        dispatch(setExpenseList(expenseObj));
      };
      getExpenses(appConfig.baseUrl + "trip?tripId=" + tripId, updateExpenses);
    }
  }, [activeTripId, dispatch, getExpenses, tripId, tripList]);

  return (
    <div>
      <TripAppBar trip={trip} />
      <ExpenseList expenses={expenseList} />
      {error && (
        <Alert severity="error" sx={{ p: 2, m: 1 }}>
          <Typography color="error">
            <strong>Error: </strong>
            {error.message}
          </Typography>
        </Alert>
      )}

      <AddExpense />
    </div>
  );
};

export default TripMainView;
